<template>
    <h3>NameComponent2</h3>

    <fieldset>
        <h4>props 接收的數據, 轉為 ref 類型</h4>
        <h5>標題: {{ fromTestObj.title }}</h5>
        <ul>
            <li v-for="(item, index) of fromTestObj.list" :key="index">{{ item }}</li>
        </ul>
        <h5>分類: {{ fromTestObj.cate.c1.type }}</h5>
    </fieldset>

    <fieldset>
        <h4>props 接收的數據, 轉為 ref 類型, 並解構</h4>
        <h5>標題: {{ title }}</h5>
        <ul>
            <li v-for="(item, index) of list" :key="index">{{ item }}</li>
        </ul>
        <h5>分類: {{ cate.c1.type }}</h5>
    </fieldset>
</template>
<script>
import { toRef } from 'vue'
export default {
    name: 'NameComponent2',
    props: ['testObj'],

    setup(props) {
        console.log('--- NameComponent2 ---')

        // props 接收的數據, 轉為 ref 類型
        console.log('props', props)  // {...}

        let fromTestObj = toRef(props, 'testObj')
        console.log('fromTestObj', fromTestObj)
        console.log('fromTestObj.value', fromTestObj.value)

        return {
            fromTestObj,
            ...fromTestObj.value,  // 將物件中的屬性解構至此物件
        }
    }
}

</script>